.feed_back__btn {
  display: inline-block;
  width: 100px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  background: #ec7676;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  margin-right: 20px; /* Відстань між кнопками */
}

.feed_back__btn:active {
  color: #000;
}
